/*
 * A comparision tree will layout each attribute of a json deep through its heirarchy with given visual queues
 * that represents a deletion, adition, or change of attribute from the other tree. The status of each node is 
 * evaluated by the parent comparision tool.
 */
import {
  Component,
  OnInit,
  Input,
  Output,
  EventEmitter
} from '@angular/core';

import {DifferentiateNodeStatus} from '../interfaces/differentiate.interfaces';

@Component({
  selector: 'differentiate-tree',
  template: `<div *ngIf="categorizeBy" 
  class="diff-heading" 
  (click)="expand($event)">
  <span class="arrow" *ngIf="collapsed">&#9658;</span>
  <span class="arrow" *ngIf="!collapsed">&#9660;</span>
  <span [textContent]="categorizeBy"></span>
  <span class="counter" [textContent]="changCounter()"></span>
</div>
<ul [class]="side" [class.child]="depth ===2 || (categorizeBy && categorizeBy.length)" [class.collapsed]="categorizeBy && collapsed">
  <li  *ngFor="let child of children" 
    (mouseout)="depth === 2 ? mouseOvered($event, false, child.index) : null"
    (mouseover)="depth === 2 ? mouseOvered($event, true, child.index) : null"
    [class.hover]="child.hover"
    [class.added]="child.status === 5" 
    [class.removed]="child.status === 6" 
    [class.type-changed]="child.status === 2" 
    [class.name-changed]="child.status === 3" 
    [class.value-changed]="child.status === 4">
    <div class='tree-node' [ngClass]="'depth-' + depth" [class.collapsed]="child.collapsed" [id] = "child.id">
      <span [title]="rightSideToolTip"
        class="do" 
        tabindex="0"
        aria-hidden="true"
        (keyup)="keyup($event)"
        (click)="advanceToRightSide(child)"
        *ngIf="showLeftActionButton && status !== child.status && child.status > 1">&#9100;</span>
      <span *ngIf='child.name && child.name!=null'
        class='name' 
        [innerHTML]="child.name.length ? child.name : '&nbsp;'">
      </span>
      <span *ngIf='child.value && child.value!=null'
        class='value' 
        [class.string]="depth > 0 && child.value && child.value.length"
        [innerHTML]="child.value ? child.value : '&nbsp;'">
      </span>
      <span [title]="leftSideToolTip"
        class="undo" 
        tabindex="0"
        aria-hidden="true"
        (keyup)="keyup($event)"
        (click)="advanceToLeftSide(child)"
        *ngIf="showRightActionButton && status !== child.status && child.status > 1">&#9100;</span>
    </div>
    <differentiate-tree *ngIf="child.children.length" 
        [level]="depth+1" 
        [status]="child.status" 
        [collapsed]="child.collapsed"
        [categorizeBy]="child.categorizeBy"
        [showLeftActionButton]="showLeftActionButton" 
        [leftSideToolTip]="leftSideToolTip"
        [showRightActionButton]="showRightActionButton" 
        [rightSideToolTip]="rightSideToolTip"
        [objectPath]="objectPath + (objectPath.length ? ',':'') + child.index"
        (onhover)="bubleup($event)"
        (onrevert)="advance($event)"
        (onexpand)="autoExpand($event)"
        [class.child-node]="child.parent != 4" 
        [children]='child.children'></differentiate-tree>
    <div *ngIf="child.status > 2" class="upper" [class.collapsed]="child.collapsed" [ngClass]="'depth-' + depth" ></div>
    <div *ngIf="child.status > 2" class="lower" [class.collapsed]="child.collapsed" [ngClass]="'depth-' + depth" ></div>
  </li>
</ul>

`,
  styles: [`:host{-webkit-box-sizing:border-box;box-sizing:border-box;width:100%}:host.root{float:left;width:50%}:host.child-node{float:left}.diff-heading{padding:5px;font-weight:700;background:rgba(0,0,0,.02);border-bottom:1px solid rgba(0,0,0,.1);color:#666;cursor:pointer}.diff-heading .arrow{color:#999;font-size:.6rem;font-weight:700}.diff-heading .counter{float:right;border-radius:50%;width:16px;text-align:center;background-color:rgba(0,0,0,.4);font-size:.8rem;color:#fff}.diff-heading:first-child{border-top:1px solid rgba(0,0,0,.1)}ul{-webkit-box-sizing:border-box;box-sizing:border-box;list-style:none;padding:0;width:100%}ul .collapsed,ul.collapsed{display:none!important}ul li .hover{background-color:rgba(0,0,0,.1)}ul li .tree-node{position:relative}ul li .tree-node.depth-0{display:none}ul li .tree-node .do,ul li .tree-node .undo{border-radius:50%;background-color:#ddd;cursor:pointer;color:#962323;font-size:1.2rem;height:18px;line-height:1.2rem;position:absolute;text-align:center;top:0;width:18px;z-index:2}ul li .tree-node .undo{right:0}ul li .tree-node .do{left:0}ul.undefined li:hover{background-color:rgba(0,0,0,.1)}ul.left-side{border-right:1px solid rgba(0,0,0,.1);margin:0}ul.left-side li{position:relative;display:table;width:100%}ul.left-side li.added .name,ul.left-side li.added .value{opacity:.2;font-style:italic}ul.left-side li.added .upper{border-radius:0 0 100%;-webkit-box-sizing:border-box;box-sizing:border-box;height:50%;position:absolute;pointer-events:none;width:50%;top:0;right:0}ul.left-side li.added .upper.depth-1{border:2px solid #245024;border-top-width:0;border-left-width:0}ul.left-side li.added .upper.depth-2{border:2px dotted #378637;border-top-width:0;border-left-width:0}ul.left-side li.added .upper.depth-3{border:1px solid #48ad48;border-top-width:0;border-left-width:0}ul.left-side li.added .upper.depth-4{border:1px dotted #57d657;border-top-width:0;border-left-width:0}ul.left-side li.added .upper.depth-5{border:1px dashed #67fa67;border-top-width:0;border-left-width:0}ul.left-side li.added .lower{border-radius:0 100% 0 0;-webkit-box-sizing:border-box;box-sizing:border-box;height:50%;position:absolute;pointer-events:none;width:50%;bottom:0;right:0}ul.left-side li.added .lower.depth-1{border:2px solid #245024;border-bottom-width:0;border-left-width:0}ul.left-side li.added .lower.depth-2{border:2px dotted #378637;border-bottom-width:0;border-left-width:0}ul.left-side li.added .lower.depth-3{border:1px solid #48ad48;border-bottom-width:0;border-left-width:0}ul.left-side li.added .lower.depth-4{border:1px dotted #57d657;border-bottom-width:0;border-left-width:0}ul.left-side li.added .lower.depth-5{border:1px dashed #67fa67;border-bottom-width:0;border-left-width:0}ul.left-side li.removed .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;width:66px;top:0;right:0;pointer-events:none}ul.left-side li.removed .upper:after{content:' - ';color:#962323;float:right;padding-right:10px;font-size:1.2rem;line-height:1.2rem}ul.left-side li.removed .lower{display:none}ul.left-side li.removed .tree-node span,ul.left-side li.type-changed .tree-node span{color:#962323}ul.left-side li.name-changed .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;width:66px;top:0;right:0;pointer-events:none}ul.left-side li.name-changed .upper:after{content:' ~ ';color:#000060;font-weight:700;float:right;padding-right:10px;font-size:1.2rem;line-height:1.2rem}ul.left-side li.name-changed .tree-node .name{color:#000060}ul.left-side li.value-changed .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;pointer-events:none;width:66px;top:0;right:0}ul.left-side li.value-changed .upper:after{content:' ~ ';color:#000060;font-weight:700;float:right;padding-right:10px;font-size:1.2rem;line-height:1.2rem}ul.left-side li.value-changed .tree-node .value{color:#000060}ul.right-side{border-left:1px solid rgba(0,0,0,.1);margin:0}ul.right-side li{position:relative;display:table;width:100%}ul.right-side li.added .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;pointer-events:none;width:90%;top:0;left:0}ul.right-side li.added .upper:after{content:'+';color:#4a4;font-weight:700;padding-left:5px;font-size:1.2rem;line-height:1.2rem}ul.right-side li.added .lower{display:none}ul.right-side li.added .tree-node span{color:#4a4}ul.right-side li.removed .name,ul.right-side li.removed .value{-webkit-text-decoration-line:line-through;text-decoration-line:line-through;-webkit-text-decoration-color:#962323;text-decoration-color:#962323}ul.right-side li.removed .upper{border-radius:0 0 0 100%;-webkit-box-sizing:border-box;box-sizing:border-box;height:50%;width:10%;position:absolute;pointer-events:none;top:0}ul.right-side li.removed .upper.depth-1{border:2px solid #600000;border-top-width:0;border-right-width:0}ul.right-side li.removed .upper.depth-2{border:2px dotted maroon;border-top-width:0;border-right-width:0}ul.right-side li.removed .upper.depth-3{border:1px solid #a00000;border-top-width:0;border-right-width:0}ul.right-side li.removed .upper.depth-4{border:1px dotted #c00000;border-top-width:0;border-right-width:0}ul.right-side li.removed .upper.depth-5{border:1px dashed #f00000;border-top-width:0;border-right-width:0}ul.right-side li.removed .lower{border-radius:100% 0 0;-webkit-box-sizing:border-box;box-sizing:border-box;height:50%;width:10%;position:absolute;pointer-events:none;bottom:0}ul.right-side li.removed .lower.depth-1{border:2px solid #600000;border-bottom-width:0;border-right-width:0}ul.right-side li.removed .lower.depth-2{border:2px dotted maroon;border-bottom-width:0;border-right-width:0}ul.right-side li.removed .lower.depth-3{border:1px solid #a00000;border-bottom-width:0;border-right-width:0}ul.right-side li.removed .lower.depth-4{border:1px dotted #c00000;border-bottom-width:0;border-right-width:0}ul.right-side li.removed .lower.depth-5{border:1px dashed #f00000;border-bottom-width:0;border-right-width:0}ul.right-side li.type-changed .tree-node span{color:#962323}ul.right-side li.name-changed .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;pointer-events:none;top:0;left:0}ul.right-side li.name-changed .upper:before{content:' ~ ';color:#000060;font-weight:700;float:right;padding-left:5px;font-size:20px;line-height:16px}ul.right-side li.name-changed .tree-node .name{color:#000060}ul.right-side li.value-changed .upper{-webkit-box-sizing:border-box;box-sizing:border-box;height:100%;position:absolute;pointer-events:none;top:0;left:0}ul.right-side li.value-changed .upper:before{content:' ~ ';color:#000060;font-weight:700;float:right;padding-left:5px;font-size:20px;line-height:16px}ul.right-side li.value-changed .tree-node .value{color:#000060}ul.child{margin-top:2px!important}ul.child li{padding-top:2px;padding-bottom:2px}ul .tree-node{-webkit-box-sizing:border-box;box-sizing:border-box;color:#7c9eb2;display:table;padding:0;position:relative;margin:0;width:100%}ul .tree-node.depth-0{padding-left:5px}ul .tree-node.depth-1{padding-left:20px}ul .tree-node.depth-2{padding-left:40px}ul .tree-node.depth-3{padding-left:60px}ul .tree-node.depth-4{padding-left:80px}ul .tree-node.depth-5{padding-left:100px}ul .tree-node.depth-6{padding-left:120px}ul .tree-node.depth-7{padding-left:140px}ul .tree-node.depth-8{padding-left:160px}ul .tree-node.depth-9{padding-left:180px}ul .tree-node.depth-10{padding-left:200px}ul .tree-node .name{color:#444;font-weight:700}ul .tree-node .name:after{content:':'}ul .tree-node .value.string:after,ul .tree-node .value.string:before{content:'"'}`],
})
export class DifferentiateTree implements OnInit{
  depth: number;

  @Input("collapsed")
  collapsed = true;

  @Input("children")
  children;

  @Input("showLeftActionButton")
  showLeftActionButton = false;

  @Input("showRightActionButton")
  showRightActionButton = false;

  @Input("status")
  status = 1;

  @Input("side")
  side = "";

  @Input("level")
  level = "0";

  @Input("objectPath")
  objectPath = "";

  @Input("categorizeBy")
  categorizeBy: string;

  @Input("leftSideToolTip")
  leftSideToolTip = "take left side";

  @Input("rightSideToolTip")
  rightSideToolTip = "take right side";

  @Output("onhover")
  onhover = new EventEmitter();

  @Output("onrevert")
  onrevert = new EventEmitter();

  @Output("onexpand")
  onexpand = new EventEmitter();

  ngOnInit() {
    this.depth = parseInt(this.level);
  }

  bubleup(event) {
    event.side = this.side;
    this.onhover.emit(event);
  }

  keyup(event) {
    const code = event.which;
    if (code === 13) {
      event.target.click();
		}
  }

  changCounter() {
    let count = 0;
    this.children.map( (item) => {
      if(item.status !== DifferentiateNodeStatus.default) {
        count++;
      }
    })
    return count;
  }

  expand(event) {
    this.onexpand.emit( this.objectPath );
  }
  autoExpand(event) {
    this.onexpand.emit(event);
  }
  advanceToRightSide(child) {
    child.path = this.objectPath + (this.objectPath.length ? ',':'') + child.index;
    this.onrevert.emit({type:"advance", node: child});
  }
  advanceToLeftSide(child) {
    child.path = this.objectPath + (this.objectPath.length ? ',':'') + child.index;
    this.onrevert.emit({type:"revert", node: child});
  }
  advance(event) {
    // bubble up the undo event.
    this.onrevert.emit(event);
  }

  mouseOvered(event, flag, i) {
    event.preventDefault();

    if (this.depth === 2) {
      event.stopPropagation();
      
      this.onhover.emit({
        hover: flag,
        index: i,
        path: this.objectPath
      });
    }
  }
}
